import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Input, Tooltip } from 'antd';

const WeightConverter = (props) => {
    const [init_processes, set_init_processes] = useState(0);
    const [record, setRecord] = useState([]);
    const init_weight = [
        {
            name: '鋼料重量',
            bg_color: '#e77f87',
            value: '鋼料重量',
            factor: '0.8',
            factor_value: 0.8,
            factor_parent: 1000000,
            factor_middle: 8,
            weight: {
                in_outer: '',
                in_length: '',
                mm_outer: '',
                mm_length: '',
                weight_outer: 0,
                weight_length: 0
            },
            total: {
                money: 0,
                weight: 0,
                per_price: 0
            }
        },
        {
            name: '碳化鎢重量',
            bg_color: '#80b769',
            value: '碳化鎢重量',
            factor: '0.016',
            factor_value: 0.016,
            factor_middle: 0.8,
            weight: {
                in_outer: '',
                in_length: '',
                mm_outer: '',
                mm_length: '',
                weight_outer: 0,
                weight_length: 0
            },
            total: {
                money: 0,
                weight: 0,
                per_price: 0
            }
        },
    ];
    const [weight, set_weight] = useState([
        {
            name: '鋼料重量',
            bg_color: '#e77f87',
            value: '鋼料重量',
            factor: '0.8',
            factor_value: 0.8,
            factor_parent: 1000000,
            factor_middle: 8,
            weight: {
                in_outer: '',
                in_length: '',
                mm_outer: '',
                mm_length: '',
                weight_outer: 0,
                weight_length: 0
            },
            total: {
                money: 0,
                weight: 0,
                per_price: 0
            }
        },
        {
            name: '碳化鎢重量',
            bg_color: '#80b769',
            value: '碳化鎢重量',
            factor: '0.016',
            factor_value: 0.016,
            factor_middle: 0.8,
            weight: {
                in_outer: '',
                in_length: '',
                mm_outer: '',
                mm_length: '',
                weight_outer: 0,
                weight_length: 0
            },
            total: {
                money: 0,
                weight: 0,
                per_price: 0
            }
        },
    ]);
    const [file_info, set_file_info] = useState(
        {
            material: '',
            titanizing: '',
            hardness: ''
        }
    );
    const price_list = [
        {
            name: 'SKD61',
            price: 500
        },
        {
            name: 'SKD11',
            price: 700
        },
        {
            name: 'DC53',
            price: 600
        },
        {
            name: 'M42',
            price: 1300
        },
        {
            name: 'SKH9',
            price: 1000
        },
        {
            name: 'SKH55',
            price: 1100
        }
    ];
    const [materialList, setMaterialList] = useState([]);
    const [stuffList, setStuffList] = useState([]);
    const [unchange, setUnchange] = useState(true);
    // const [weight, set_weight] = useState({
    //     in_outer: 0,
    //     in_length: 0,
    //     mm_outer: 0,
    //     mm_length: 0,
    //     weight_outer: 0,
    //     weight_length: 0
    // })
    // const [total, set_total] = useState({
    //     money: 0,
    //     weight: 0,
    //     per_price: 0
    // })
    useEffect(() => {
        axios
            .get('/material', {
                params: {
                    file_id: props.file_id
                }
            })
            .then(response => {
                let material_name = null;
                let material = null;
                let material_id = null;
                material = response.data.material.shift() || {};
                material_name = material.hasOwnProperty('label') ? material.label : '';
                material_id = material.hasOwnProperty('material_id') ? material.material_id : '';
                let price_temp = price_list.find(price => material_name.indexOf(price.name) !== -1) || { price: 0 };
                set_weight(prev_weight => (
                    prev_weight.map(prev_weight_each => (
                        {
                            ...prev_weight_each,
                            total: {
                                ...prev_weight_each.total,
                                per_price: (price_temp.hasOwnProperty('price') && prev_weight_each.total.per_price == 0 ? price_temp.price : prev_weight_each.total.per_price)
                            }
                        }
                    ))
                ))
                let titanizing = null
                titanizing = response.data.titanizing.shift() || {};
                titanizing = titanizing.hasOwnProperty('label') ? titanizing.label : '';
                let hardness = null
                hardness = response.data.hardness.shift() || {};
                hardness = hardness.hasOwnProperty('label') ? hardness.label : '';

                set_file_info(
                    {
                        material_id: material_id,
                        material: material_name,
                        titanizing: titanizing,
                        hardness: hardness
                    }
                )
                set_init_processes(prev_init_processes => prev_init_processes + 1)
            })
        axios
            .get('/quotationBusiness/api/weight',
                {
                    params: {
                        file_id: props.file_id
                    }
                }
            ).then(response => {
                if (response.data != null && Array.isArray(response.data)) {
                    if(response.data.length === 0){
                        set_weight(init_weight)
                    }else{
                        set_weight(response.data)
                    }
                }
                set_init_processes(prev_init_processes => prev_init_processes + 1)
            })
        axios
            .get('/quotationBusiness/api/weight/list').then(response => {
                setMaterialList(response.data.material)
                setStuffList(response.data.stuff)
            })
        axios
            .get('/quotationBusiness/api/weight/record', {
                params: {
                    file_id: props.file_id
                }
            }).then(response => {
                setRecord(response.data)
            })
    }, [])
    const handle_on_weight = (e, index) => {
        set_is_sent(true)
        if (!isNaN(parseFloat(e.target.value))) {
            if (e.target.value.slice(-1) === ".") {
                set_weight(
                    prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => prev_weight_index === index ? (
                        {
                            ...prev_weight_each,
                            weight: {
                                ...prev_weight_each.weight,
                                [e.target.name]: e.target.value,
                            }
                        }
                    ) : prev_weight_each)
                )
            } else {
                set_weight(
                    prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => prev_weight_index === index ? (
                        {
                            ...prev_weight_each,
                            weight: {
                                ...prev_weight_each.weight,
                                [e.target.name]: parseFloat(e.target.value),
                            }
                        }
                    ) : prev_weight_each)
                )
            }
        } else if (e.target.value === '') {
            set_weight(
                prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => prev_weight_index === index ? (
                    {
                        ...prev_weight_each,
                        weight: {
                            ...prev_weight_each.weight,
                            [e.target.name]: 0,
                        }
                    }
                ) : prev_weight_each)
            )
        } else {
            set_weight(
                prev_weight => (
                    [
                        ...prev_weight,
                    ]
                )
            )
        }
    }
    const handle_on_mm = (e, index) => {
        set_is_sent(true)
        if (!isNaN(parseFloat(e.target.value))) {
            if (e.target.value.replace(/0*$/, "").slice(-1) === ".") {
                set_weight(
                    prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ?
                        {
                            ...prev_weight_each,
                            weight: {
                                ...prev_weight_each.weight,
                                ['mm_' + e.target.name]: e.target.value,
                                ['in_' + e.target.name]: (parseFloat(e.target.value) / 25.4).toFixed(2)
                            }
                        } : prev_weight_each
                    ))
                )
            } else {
                set_weight(
                    prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ?
                        {
                            ...prev_weight_each,
                            weight: {
                                ...prev_weight_each.weight,
                                ['mm_' + e.target.name]: parseFloat(e.target.value),
                                ['in_' + e.target.name]: (parseFloat(e.target.value) / 25.4).toFixed(2)
                            }
                        } : prev_weight_each
                    ))
                )
            }
        } else if (e.target.value === '') {
            set_weight(
                prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ?
                    {
                        ...prev_weight_each,
                        weight: {
                            ...prev_weight_each.weight,
                            ['in_' + e.target.name]: 0,
                            ['mm_' + e.target.name]: e.target.value
                        }
                    } : prev_weight_each
                ))
            )
        } else {
            set_weight(
                prev_weight => ([
                    ...prev_weight
                ])
            )
        }
    }
    const handle_on_in = (e, index) => {
        set_is_sent(true)
        if (!isNaN(parseFloat(e.target.value))) {
            if (e.target.value.replace(/0*$/, "").slice(-1) === ".") {
                set_weight(
                    prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ?
                        {
                            ...prev_weight_each,
                            weight: {
                                ...prev_weight_each.weight,
                                ['in_' + e.target.name]: e.target.value,
                                ['mm_' + e.target.name]: (parseFloat(e.target.value) * 25.4).toFixed(2)
                            }
                        } : prev_weight_each
                    ))
                )
            } else {
                set_weight(
                    prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ?
                        {
                            ...prev_weight_each,
                            weight: {
                                ...prev_weight_each.weight,
                                ['in_' + e.target.name]: parseFloat(e.target.value),
                                ['mm_' + e.target.name]: (parseFloat(e.target.value) * 25.4).toFixed(2)
                            }
                        } : prev_weight_each
                    ))
                )
            }
        } else if (e.target.value === '') {
            set_weight(
                prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ?
                    {
                        ...prev_weight_each,
                        weight: {
                            ...prev_weight_each.weight,
                            ['in_' + e.target.name]: e.target.value,
                            ['mm_' + e.target.name]: 0
                        }
                    } : prev_weight_each
                ))
            )
        } else {
            set_weight(
                prev_weight => ([
                    ...prev_weight
                ])
            )
        }
    }
    const handle_on_change_per_price = (e, index) => {
        set_weight(prev_weight => prev_weight.map((prev_weight_each, prev_weight_index) => (prev_weight_index === index ? {
            ...prev_weight_each,
            total: {
                ...prev_weight_each.total,
                per_price: parseFloat(e.target.value)
            }
        } : prev_weight_each)))
    }
    const addWeight = (e) => {
        let material_id = "";
        let stuff_id = "";
        materialList.map((value, index) => (
            value.material_code === file_info.material_id ? material_id = value.material_id : null
        ))
        stuffList.map((value, index) => (
            value.stuff_name === e.target.attributes.stuff_name.value ? stuff_id = value.stuff_id : null
        ))
        let data = {
            per_price: weight[0].total.per_price,
            total_weight: weight[0].total.weight,
            total_price: weight[0].total.money,
            material_id: material_id,
            stuff_id: stuff_id,
            file_id: props.file_id,
            weight: JSON.stringify(weight) 
        }
        axios
            .post('/quotationBusiness/api/weight/record', data)
            .then(response => {
                axios
                    .get('/quotationBusiness/api/weight/record', {
                        params: {
                            file_id: props.file_id
                        }
                    }).then(response => {
                        setUnchange(true)
                        setRecord(response.data)
                    })
            })
    }
    const handleDeleteRecord = (index, weight_record_id) => {

        axios
            .delete('/quotationBusiness/api/weight/record', {
                data: {
                    weight_record_id: weight_record_id
                }
            })
            .then(response => {
                axios
                    .get('/quotationBusiness/api/weight/record', {
                        params: {
                            file_id: props.file_id
                        }
                    }).then(response => {
                        setUnchange(true)
                        setRecord(response.data)
                    })
            })
    }
    const handleChangeMaterial = (e) => {
        console.log(weight)
        let weight_tmp = [...weight]

        let fileInfo = { ...file_info }
        materialList.map((value, index) => {
            if (value.material_code === e.target.value) {
                fileInfo.material_id = e.target.value
                fileInfo.materail = value.material_name
                weight_tmp.map((w_value, w_index) => {
                    if (w_value.name === "鋼料重量") {
                        let total = w_value['total']
                        total.per_price = value.material_price
                        w_value['total'] = total;
                    }
                })
            }
        })
        set_weight(weight_tmp)
        set_file_info(fileInfo)
    }
    const handleImport = (index, material_id) => {
        set_weight(record[index]['weight'])
        materialList.map((material_value, material_index) => (
            material_value.material_id === material_id ? set_file_info(prevState => ({...prevState, material_id: material_value.material_code})) : null
        ))
    }
    const [is_sent, set_is_sent] = useState(true);
    useEffect(() => {
        if (materialList.length !== 0 && record.length !== 0 && unchange) {
            setUnchange(false)
            let record_temp = [...record]
            record_temp.map((value, index) => (
                materialList.map((material_value, material_index) => (
                    material_value.material_id === value.material_id ? value.material_name = material_value.material_name : null
                ))
            ))
            setRecord(record_temp)
        }
    }, [materialList, record])
    useEffect(() => {
        if (init_processes != 2) return;
        if (is_sent) {
            set_weight(prev_weight => prev_weight.map((prev_weight_each) => {
                let weight_total = Math.pow((parseFloat(prev_weight_each.weight.mm_outer) + prev_weight_each.weight.weight_outer), 2) * (parseFloat(prev_weight_each.weight.mm_length) + prev_weight_each.weight.weight_length) * prev_weight_each.factor * prev_weight_each.factor_middle / (prev_weight_each.factor_parent || 1);
                return {
                    ...prev_weight_each,
                    total: {
                        ...prev_weight_each.total,
                        weight: weight_total.toFixed(2),
                        money: weight_total * prev_weight_each.total.per_price.toFixed(2)
                    }
                }
            }))
            set_is_sent(false)
        } else {
            axios
                .patch('/quotationBusiness/api/weight',
                    {
                        file_id: props.file_id,
                        weight: JSON.stringify(weight)
                    }
                )
                .then(response => {
                    set_is_sent(true)
                })
        }
    }, [weight])
    return (
        <div className='card'>
            <div className='card-header'>
                <div className='card-title h5'>重量轉換</div>
            </div>
            <div className='card-body'>
                <div className='row'>
                    {weight.map((weight_each, weight_index) => (
                        <div className='col-xl'>
                            {/* <div className="btn-group btn-group-toggle" data-toggle="buttons">
                                {listMessages.map((radio, idx) => (
                                    <label className={(idx % 2 ? 'btn btn-outline-success' : 'btn btn-outline-danger') + (listMessageValue === radio.value ? " active" : "")}>
                                        <input
                                            key={idx}
                                            id={`radio-${idx}`}
                                            type="radio"
                                            name="radio"
                                            value={radio.value}
                                            checked={listMessageValue === radio.value}
                                            onChange={(e) => {
                                                setListMessageValue(e.currentTarget.value)
                                            }}
                                            disabled={props.readonly}
                                        />
                                        {radio.name}
                                    </label>
                                ))}
                            </div> */}
                            <div className='row'>
                                <div className='col-auto alert' style={{ backgroundColor: weight_each.bg_color }}>
                                    <h5 className='my-0' style={{ fontWeight: 'bold', color: 'white' }}>{weight_each.name}</h5>
                                </div>
                            </div>
                            <div className='row'>
                                {
                                    weight_each.name === '鋼料重量' ?
                                        <>
                                            <span className='col-form-label col-auto pr-0'>材質：</span>
                                            <div className='col-2 mb-1 pl-0'>
                                                <select class="custom-select" id="inputGroupSelect01" disabled={props.readonly} onChange={e => handleChangeMaterial(e)} value={file_info.material_id}>
                                                    {
                                                        materialList.map((value, index) => (
                                                            <option value={value.material_code} material_id={value.material_id}>{value.material_name}</option>
                                                        ))
                                                    }
                                                </select>
                                            </div>
                                        </> : <span className='col-form-label col-auto'>材質：{file_info.material}</span>
                                }

                                <span className='col-form-label col-auto px-0'>金額：</span>
                                <div className='col-2 px-0'><Input type="text" value={weight_each.total.per_price} onChange={e => handle_on_change_per_price(e, weight_index)} disabled={!props.readonly} /></div>

                                <span className='col-form-label col-auto'>鍍鈦：{file_info.titanizing}</span>
                                <span className='col-form-label col-auto'>硬度：{file_info.hardness}</span>
                            </div>
                            <div className='row'>
                                <div className='col-auto alert alert-secondary'>
                                    <span>尺寸</span><br />
                                    <span>單位</span><br />
                                    <span>轉換</span>
                                </div>
                                <div className='col'>
                                    <div className="form-group form-inline" style={{ padding: '0px 0px 12px 0px ' }}>
                                        <div className='row'>
                                            <div className='col-6'>
                                                <Input addonBefore="最大外徑(英制)" suffix="in" name="outer" placeholder='請輸入' value={weight_each.weight.in_outer} onChange={e => handle_on_in(e, weight_index)} disabled={props.readonly} />
                                            </div>
                                            <div className='col-6'>
                                                <Input addonBefore="最大長度(英制)" suffix="in" name="length" placeholder='請輸入' value={weight_each.weight.in_length} onChange={e => handle_on_in(e, weight_index)} disabled={props.readonly} />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="m-0 form-group form-inline">
                                        <div className='row '>
                                            <div className='col-6'>
                                                <Input addonBefore="最大外徑(公制)" suffix="mm" name="outer" placeholder='請輸入' value={weight_each.weight.mm_outer} onChange={e => handle_on_mm(e, weight_index)} disabled={props.readonly} />
                                            </div>
                                            <div className='col-6'>
                                                <Input addonBefore="最大長度(公制)" suffix="mm" name="length" placeholder='請輸入' value={weight_each.weight.mm_length} onChange={e => handle_on_mm(e, weight_index)} disabled={props.readonly} />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className='row'>
                                <div style={{ padding: '0rem' }} className='col-1 d-flex align-items-center'>
                                    <span style={{ padding: '0.5rem', margin: '0rem' }} className='alert alert-secondary'><label style={{ margin: '0rem' }}>重量計算</label></span>
                                </div>
                                <div className='col'>
                                    <div className='row d-flex justify-content-center'>
                                        <span style={{ fontSize: 24, padding: '0rem 0.2rem' }} className='col-form-label col-auto mx-0 my-1'>
                                            (
                                        </span>
                                        <div className="form-group input-group input-group-sm col-2 p-0 flex-nowrap">
                                            <input type="text" className='form-control' disabled placeholder='外徑' style={{ fontSize: 16 }} value={weight_each.weight.mm_outer} />
                                        </div>
                                        <span className='col-form-label col-auto text-nowrap my-1' style={{ fontSize: 20, padding: '0rem 0.2rem' }}>+</span>
                                        <div className='form-group input-group input-group-sm col-1 p-0 flex-nowrap'>
                                            <input type="tel" step="0.01" className='form-control' style={{ fontSize: 16, padding: '0.1rem' }} name="weight_outer" value={weight_each.weight.weight_outer} onChange={e => handle_on_weight(e, weight_index)} disabled={props.readonly} />
                                        </div>
                                        <span className='col-form-label col-auto  m-0 text-nowrap my-1' style={{ fontSize: 20, padding: '0rem 0.2rem' }}>)<sup>2</sup> X (</span>
                                        <div className="form-group input-group input-group-sm col-2 p-0 flex-nowrap">
                                            <input type="text" className='form-control' disabled placeholder='長度' style={{ fontSize: 16 }} value={weight_each.weight.mm_length} />
                                        </div>
                                        <span className='col-form-label col-auto p-0 m-0 text-nowrap my-1' style={{ fontSize: 20 }}>＋</span>
                                        <div className='form-group input-group input-group-sm col-1 p-0 flex-nowrap'>
                                            <input type="tel" step="0.01" className='form-control ' style={{ fontSize: 16, padding: '0.1rem' }} name="weight_length" value={weight_each.weight.weight_length} onChange={e => handle_on_weight(e, weight_index)} disabled={props.readonly} />
                                        </div>
                                        <span className='col-form-label col-auto p-0 m-0 text-nowrap my-1' style={{ fontSize: 20, padding: '0rem 0.2rem' }}>) X {weight_each.factor || ''} X {weight_each.factor_middle}</span>
                                    </div>
                                    {weight_each.hasOwnProperty('factor_parent') && (
                                        <>
                                            <hr style={{ backgroundColor: 'black', margin: '0.1rem' }} />
                                            <div className="form-group d-flex justify-content-center" style={{ fontSize: 20 }}>1,000,000</div>
                                        </>
                                    )}
                                </div>
                            </div>
                            <div className='row d-flex justify-content-end'>
                                <div className='col-5'>
                                    <div className="form-group input-group">
                                        <div className="input-group-prepend">
                                            <span style={{ fontSize: 18 }} className="input-group-text">總重量</span>
                                        </div>
                                        <input style={{ fontSize: 18 }} type="text" className='form-control' disabled value={weight_each.total.weight} />
                                    </div>
                                </div>
                                <div className='col-5'>
                                    <div className="form-group input-group">
                                        <div className="input-group-prepend">
                                            <span style={{ fontSize: 18 }} className="input-group-text">總金額</span>
                                        </div>
                                        <input style={{ fontSize: 18 }} type="text" className='form-control' disabled value={weight_each.total.money} />
                                    </div>
                                </div>
                                {
                                    weight_each.name === '鋼料重量' ?
                                        <div className='col-auto'>
                                            <button type="button" disabled={props.readonly} onClick={e => addWeight(e)} stuff_name={"鋼料"} class="btn btn-secondary">新增</button>
                                        </div>
                                        : null
                                }
                            </div>
                            {
                                weight_each.name === '鋼料重量' ?
                                    <>
                                        <hr style={{ backgroundColor: '#b1b1b1', margin: '1rem 0rem' }} />
                                        {
                                            record.map((value, index) => (
                                                <div className='row my-2'>
                                                    <div className='col-12 '>
                                                        <div class="card">
                                                            <div class="card-body" style={{ backgroundColor: '#fff9ed' }}>
                                                                <div className='row  d-flex'>
                                                                    <div className='d-flex align-self-center col-1 pl-1'>
                                                                        <span aria-hidden="true" style={{ fontSize: 30 }}>{index + 1}</span>
                                                                    </div>
                                                                    <div className='col-5 mr-2'>
                                                                        <div className='row '>
                                                                            <div className="form-group input-group">
                                                                                <div className="input-group-prepend">
                                                                                    <span style={{ fontSize: 18, backgroundColor: 'white' }} className="input-group-text">材質</span>
                                                                                </div>
                                                                                <input
                                                                                    style={{ fontSize: 18, backgroundColor: 'white' }}
                                                                                    type="text"
                                                                                    className='form-control'
                                                                                    value={value.material_name}
                                                                                    disabled />
                                                                            </div>
                                                                        </div>
                                                                        <div className='row '>
                                                                            <div className="form-group input-group">
                                                                                <div className="input-group-prepend">
                                                                                    <span style={{ fontSize: 18, backgroundColor: 'white' }} className="input-group-text">總重量</span>
                                                                                </div>
                                                                                <input style={{ fontSize: 18, backgroundColor: 'white' }} type="text" value={value.total_weight} className='form-control' disabled />
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                    <div className='col-5 mr-auto '>
                                                                        <div className='row '>
                                                                            <div className="form-group input-group">
                                                                                <div className="input-group-prepend">
                                                                                    <span style={{ fontSize: 18, backgroundColor: 'white' }} className="input-group-text">金額</span>
                                                                                </div>
                                                                                <input style={{ fontSize: 18, backgroundColor: 'white' }} type="text" className='form-control' value={value.per_price} disabled />
                                                                            </div>
                                                                        </div>
                                                                        <div className='row '>
                                                                            <div className="form-group input-group">
                                                                                <div className="input-group-prepend">
                                                                                    <span style={{ fontSize: 18, backgroundColor: 'white' }} className="input-group-text">總金額</span>
                                                                                </div>
                                                                                <input style={{ fontSize: 18, backgroundColor: 'white' }} type="text" value={value.total_price} className='form-control' disabled />
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                    <div className='col-auto' >
                                                                        <div className='row '>
                                                                            <button type="button" disabled={props.readonly} class="close" onClick={e => handleDeleteRecord(index, value.weight_record_id)} aria-label="Close">
                                                                                <span aria-hidden="true" style={{ fontSize: 40 }}>&times;</span>
                                                                            </button>
                                                                        </div>
                                                                        <div className='row '>
                                                                            <button type="button" class="close" title='帶入' onClick={e => handleImport(index, value.material_id)} disabled={!props.readonly} aria-label="Close">
                                                                                <span aria-hidden="true" style={{ fontSize: 22 }}>📤</span>
                                                                            </button>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            ))
                                        }

                                    </>
                                    : null
                            }
                        </div>
                    ))}
                </div>
            </div>
        </div >)
}
export default WeightConverter;